@charset "UTF-8";

$sidebarwidth: 80px;
$sidebarpaddingright: 20px;
$sidebarmarginright: 30px;

body {
  font-family: "Palatino Linotype", "Book Antiqua", Palatino, serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
}

a {
  color: #227ce8;
}

.sidebar {
  position: fixed;
  top: 0px;
  padding: 15px;
  z-index: 1;
  width: $sidebarwidth;
  font-family: Helvetica, sans-serif;
  height: 100vh;
  a {
    text-decoration: none;
  }
  img {
    padding: 0 3px;
    border: 0;
  }
  .avatar {
    padding: 0;
    margin-left: 2px;
    border-radius: 5px;
  }
  .name {
    text-transform: uppercase;
    margin: 10px 0 20px 0;
    #fname {
      padding-left: 1px;
      font-size: 23px;
      letter-spacing: 5px;
      color: black;
    }
    #lname {
      font-size: 17px;
      font-weight: bold;
      letter-spacing: 1px;
      color: black;
    }
  }
  .meta {
    a {
      margin-left: 0px;
      color: black;
      &:hover {
        color: #227ce8;
        transition: 0.25s;
      }
      transition: 0.25s;
    }
    margin-left: 4px;
    .icon {
      margin-right: 6px;
    }
  }
  .sections {
    margin: 15px 0 0 0;
    a {
      padding: 3px 0;
      color: black;
      font-weight: bold;
      font-size: 12px;
      display: block;
      &:hover {
        color: #227ce8;
        transition: 0.25s;
      }
      transition: 0.25s;
    }
    ul {
      margin: 0;
      padding: 0;
    }
    li {
      list-style: none;
      margin-bottom: 7px;
    }
  }
}

.content {
  text-align: justify;
  max-width: 650px;
  margin-left: $sidebarwidth + $sidebarmarginright + $sidebarpaddingright;
  margin-right: 20px;
  margin-bottom: 50px;
  iframe {
    max-width: 100%;
  }
  h1,
  h2,
  h3,
  h4 {
    color: #333;
    font-family: "Yanone Kaffeesatz", Helvetica, sans-serif;
    font-weight: 400;
    a {
      color: #111;
      text-decoration: none;
      transition: 0.25s;
    }
    transition: 0.25s;
  }
  h1 {
    font-size: 36px;
    margin: 18px 0 5px 0;
  }
  h2 {
    font-size: 30px;
    margin-bottom: 15px;
  }
  h3 {
    color: #777;
    font-size: 25px;
    padding-bottom: 5px;
    margin-bottom: 10px;
  }
  p {
    font-size: 16px;
    margin-top: 0;
    margin-bottom: 1.2em;
    line-height: 1.7em;
    code {
      max-width: 100%;
      display: inline-block;
      font-size: 12px;
      background-color: #eeeeee;
      border: 1px solid #777777;
      border-radius: 3px;
      padding: 2px 3px;
    }
  }
  img {
    padding: 3px;
    border: 1px solid #eeeeee;
    max-width: 100%;
  }
  hr {
    border: 0;
    height: 6px;
    background-image: url("/res/hr.png");
    background-repeat: repeat-x;
    margin: 2em 3em;
  }
  li {
    margin-bottom: 10px;
    line-height: 1.4em;
  }
  blockquote {
    font-style: italic;
    font-size: 14px;
    padding: 0.25em 1em;
    margin: 1em 1em 0 1em;
    color: #444;
    cite {
      &:before {
        content: "â€” ";
      }
      display: block;
      margin-top: 0.5em;
      text-align: right;
      color: #111;
      font-style: normal;
    }
  }
  .byline {
    margin-bottom: 17px;
    font-family: "Yanone Kaffeesatz", Helvetica, sans-serif;
    padding-left: 0px;
    font-size: 17px;
    color: #777;
  }
  .aside {
    background-color: #eeeeee;
    padding: 14px;
    font-size: 14px;
    margin: 5px 0 15px 0;
    font-family: Helvetica, sans-serif;
    line-height: 1.6em;
  }
  .listing {
    margin: 10px 0 0 0;
    padding: 0;
    font-family: Helvetica, sans-serif;
    ul {
      margin: 0;
      padding: 0;
    }
    li {
      font-size: 15px;
      list-style: none;
      padding: 10px 18px;
      margin: 0;
      color: #777777;
      border-bottom: 1px solid #eee;
      span {
        float: right;
      }
    }
    a {
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .meta {
    margin: 30px 0;
    padding: 10px 0;
    font-family: Helvetica, sans-serif;
    p {
      line-height: 1.4;
      font-size: 14px;
      margin-top: 0px;
    }
    img {
      position: relative;
      float: left;
      margin-top: 0px;
      margin-right: 10px;
      margin-left: -60px;
      border-radius: 4px;
    }
    .copy {
      padding-left: 60px;
    }
    iframe {
      padding-left: 5px;
    }
  }
  .talk-design .color {
    div {
      float: left;
      margin-right: 10px;
      padding: 5px 15px;
      border: 1px solid #000;
      font-family: Helvetica, sans-serif;
      font-size: 12px;
      font-weight: bold;
    }
    .white {
      color: #fff;
    }
  }
}

.language-select {
  float: right;
  img {
    border: none !important;
    height: 30px;
    width: 60px;
  }
}

table {
  border-collapse: collapse;
  th,
  td {
    border: 1px solid #dadada;
    padding: 4px;
  }
  th {
    padding: 8px;
  }
}

@media (prefers-color-scheme: dark) {
  a {
    color: #598ecd;
  }

  body {
    background: #202020;
    color: #fff;
  }

  .sidebar {
    background: #161616;
    //border-right: 1px solid white;
    box-shadow: 5px 0px 10px #161616;
    .name {
      #fname {
        color: white;
      }
      #lname {
        color: white;
      }
    }
    .meta {
      color: white;
      &:hover {
        color: #227ce8;
      }
      i {
        color: white;
      }
    }
    .sections {
      a {
        color: white;
      }
      &:hover {
        color: #227ce8;
      }
    }
  }

  .content {
    text-align: justify;
    h1,
    h2,
    h3,
    h4 {
      color: #e9e9e9;
      a {
        color: #e9e9e9;
      }
      &:hover {
        color: #3b87fa;
      }
    }
    img {
      border: 1px solid #060606;
    }
    blockquote {
      color: #aaa;
    }
    .listing {
      li {
        color: #808080;
        border-bottom: 1px solid #0f0f0f;
      }
    }
  }
  table {
    border-collapse: collapse;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    th,
    td {
      border: 1px solid black;
      padding: 4px;
    }
    th {
      padding: 8px;
      background: #0e0e0e;
    }
    td {
      background: #303030;
    }
  }
}
